// tab name
export const Tabs = {
    TASK: "taskTab",
    TOMATO: "tomatoTab",
};
// state data name
export const dataTypes = {
    focusTime: "focusTime",
    tomatoNum: "tomatoNum",
    compeletedRate: "compeletedRate",
    compeletedTaskNum: "compeletedTaskNum",
};

//  title & unit
export const Description = {
    [Tabs.TOMATO]: [
        {
            timeframe: "week",
            type: dataTypes.focusTime,
            unit: "hr",
            description: "",
        },
        {
            timeframe: "day",
            type: dataTypes.focusTime,
            unit: "hr",
            description: "",
        },
        {
            timeframe: "week",
            type: dataTypes.tomatoNum,
            unit: "",
            description: "",
        },
        {
            timeframe: "day",
            type: dataTypes.tomatoNum,
            unit: "",
            description: "",
        },
    ],
    [Tabs.TASK]: [
        {
            timeframe: "week",
            type: "compeletedTaskNum",
            unit: "",
            description: "",
        },
        {
            timeframe: "day",
            type: "compeletedTaskNum",
            unit: "",
            description: "",
        },
        {
            timeframe: "week",
            type: dataTypes.compeletedRate,
            unit: "%",
            description: "",
        },
        {
            timeframe: "day",
            type: "compeletedRate",
            unit: "%",
            description: "",
        },
    ],
};

/**
 * (tab) (description)
 * @param {String} tab 
 * @returns object
 */
export function Tab2types(tab) {
    return Description[tab];
}
