import { Modal } from "../..";

function MessageModal({ message }) {
    return (
        <Modal.Wrapper>
            <Modal.Title>{message?.title || ""}</Modal.Title>
            <Modal.Body>
                <p>{message?.message || ""}</p>
            </Modal.Body>
            <Modal.ComfirmFooter />
        </Modal.Wrapper>
    );
}

export default MessageModal;
