import { useEffect, useRef } from "react";

function useInterval(callback, delay) {
    const savecallback = useRef();
    //  callback 
    useEffect(() => {
        savecallback.current = callback;
    }, [callback]);

    useEffect(() => {
        function tick() {
            savecallback.current();
        }
        if (delay !== null) {
            let id = setInterval(tick, 1000);
            return () => {
                clearInterval(id);
            };
        }
    }, [delay]);
}
export default useInterval;
