import { SET_ALARM, TOGGLE_ALARM, HAS_ALARM } from "./actionType";

export function setAlarm(group, id) {
    return {
        type: SET_ALARM,
        payload: { group, id },
    };
}
// 
export function toggleAlarm(boolean) {
    return {
        type: TOGGLE_ALARM,
        payload: boolean,
    };
}
// 
export function hasAlarm(boolean) {
    return {
        type: HAS_ALARM,
        payload: boolean,
    };
}
