import { adjustOpacity, theme } from "@constants/theme";
import { Tabs } from "@pages/AnalysisPage/type";

export const custom_canvas_background_color = {
    id: "custom_canvas_background_color",
    beforeDraw: (chart) => {
        const ctx = chart.canvas.getContext("2d");
        const chartArea = chart.chartArea;
        ctx.save();
        // 
        // ctx.globalCompositeOperation = "destination-over";
        ctx.fillStyle = theme.greyscale.black_0;
        ctx.shadowColor = adjustOpacity(theme.greyscale.black_1000, 0.5);
        ctx.shadowBlur = 20;
        ctx.shadowOffsetY = 5;
        ctx.fillRect(
            chartArea.left,
            chartArea.top,
            chartArea.right - chartArea.left,
            chartArea.bottom - chartArea.top
        );
        ctx.fill();
        ctx.restore();
    },
};

const plugins = (tab) => ({
    legend: {
        display: false,
    },
    tooltip: {
        callbacks: {
            label: (context) => {
                let label = context.dataset.label;
                let value = context.parsed.y;
                let unit = "";
                if (label === "") {
                    unit = "";
                } else {
                    unit = "%";
                }
                return `${label} : ${value} ${unit}`;
            },
        },
    },
    custom_canvas_background_color,
    title: {
        display: true,
        text: () => {
            if (tab === Tabs.TASK) {
                return "";
            }
            return "";
        },
    },
});

// X 
const scaleX_timeset = {
    unit: "day",
    // x 
    displayFormats: {
        day: "MMM Do",
    },
    // tooltip 
    tooltipFormat: "MMM Do",
    round: "day",
};
const scaleX_ticks = {
    font: {
        size: 10,
    },
    color: theme.text.dark,
};
// Y 
const scaleY_ticks = (tab) => ({
    color: theme.primary.Default,
    callback: function (value, index, ticks) {
        if (tab === Tabs.TASK) {
            return value + "%";
        }
        return value;
    },
});
/**
 *  tab 
 */
export const options = (tab) => ({
    responsive: true,
    maintainAspectRatio: false,
    plugins: plugins(tab),
    scales: {
        x: {
            type: "time",
            time: scaleX_timeset,
            grid: {
                display: false,
            },
            ticks: scaleX_ticks,
        },
        y: {
            ticks: scaleY_ticks(tab),
            beginAtZero: true,

            // todo : 
            suggestedMax: yaxisValueRange(tab),
            grid: {
                color: adjustOpacity(theme.primary.Default, 0.4),
            },
        },
    },
});
function yaxisValueRange(tab) {
    let max = 0;
    if (tab === Tabs.TOMATO) {
        max = 25;
        return max;
    }
    max = 100;
    return max;
}
// 
export const style = {
    marginBottom: "30px",
    width: "100%",
    padding: "0 .3em",
};
